        },
        payment() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                let payments = {};
                for (var i = 0; i < this.options.length; i++) {
                    payments[this.options[i].value] = this.options[i].label;
                }
                this.payments = payments;
            });
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                this.orderSourceMap = orderSourceMap;
            });
        },
        // 订单详情
        btn(item) {
            if (item.orderType == 3 || item.orderType == 4) {
                this.$router.push({
                    path: '/rentInfo_long',
                    query: {
                        orderRoomId: item.id
                    }
                });
            } else {
                this.$router.push({
                    name: 'Details',
                    query: {
                        orderRoomId: item.id
                    }
                });
            }
        },
        reloadPage(data) {
            this.list = [];
            this.currentPage = 1;
            this.statusPage = data.statusPage;
            this.queryValue = data.queryValue;
            this.$store.commit({
                type: 'm_getOrder',
                m_queryValue: this.queryValue,
                m_statu: this.statusPage || '1'
            });
            this.loadMore();
        },
        // 下拉刷新
        loadTop() {
            this.list = [];
            this.orderPageList();
            this.$refs.loadmore.onTopLoaded();
        },
        // 上拉加载
        loadBottom() {
            this.currentPage += 1;
            this.loadMore();
        },
        // 下面刷新加载
        orderPageList() {
            let hotelId = this.$route.query.hotelId;
            let param = {
                pageSize: 10,
                pageNum: 1,
                status: this.$route.query.roomId ? '' : this.statusPage || 1,
                hotelId: hotelId,
                searchValue: this.queryValue,
                roomId: this.$route.query.roomId || ''
            };
            PostBusinessOrderroomList(param).done((res) => {
                this.deal(res);
            });
        },
        // 上面
        loadMore() {
            let hotelId = this.$route.query.hotelId;
            this.history_unRoom = JSON.parse(localStorage.getItem('history_unRoom'));
            if (this.statusPage != '1' || this.history_unRoom == true) {
                this.show_unRoom = false;
                let param = {
                    pageSize: 10,
                    pageNum: this.currentPage,
                    status: this.$route.query.roomId ? '' : this.statusPage || 1,
                    hotelId: hotelId,
                    searchValue: this.queryValue,
                    roomId: this.$route.query.roomId || ''
                };
                PostBusinessOrderroomList(param).done((res) => {
                    this.deal(res);
                });
            } else {
                this.show_today = true;
                this.show_notoday = true;
                this.show_unRoom = true;
                let param = {
                    hotelId: hotelId,
                    searchValue: this.queryValue,
                    roomId: this.$route.query.roomId || ''
                };
                PostBusinessOrderroomUnroomList(param).done((res) => {
                    this.deal(res, 'noRoom');
                });
            }
        },
        deal(result, title) {
            // await this.payment();
            if (!title) {
                if (result.total == 0) {
                    this.showTop = true;
                    this.showButtom = false;
                    this.total = result.total;
                    this.$emit('back', this.total, this.statusPage);
                } else if (result.total <= 10) {
                    this.allLoaded = true;
                    this.list = result.data;
                    this.showTop = false;
                    this.showButtom = true;
                    this.total = result.total;
                    this.$emit('back', this.total, this.statusPage);
                } else if (result.total > 10) {
                    this.showTop = false;
                    this.courrentPage = 0;
                    this.allLoaded = false; // 可以进行上拉
                    this.total = result.total;
                    this.$emit('back', this.total, this.statusPage);
                    this.$refs.loadmore.onTopLoaded();
                    this.list = this.list.concat(result.data);
                    if (this.list.length == this.total) {
                        this.showButtom = true;
                    } else {
                        if (this.list.length == result.total) {
                            this.bottomAllLoaded = false;
                            this.showButtom = true;
                        } else {
                            this.showButtom = false;
                            this.$refs.loadmore.onBottomLoaded();
                        }
                    }
                }
            } else {
                this.$emit('back', result.data.length, this.statusPage);
                this.list = result.data;
            }
            for (let i = 0; i < this.list.length; i++) {
                let item = this.list[i];
                let _day = dayjs(item.order.startTime).add(1, 'day').format('YYYY-MM-DD') + ' ' + '08:00:00';
                item.next_day = dayjs(_day).unix();
                if (item.orderType !== 3) {
                    var eTime = new Date(item.order.endTime.replace(/-/g, '/'));
                    var sTime = new Date(item.order.startTime.replace(/-/g, '/'));
                    var sHTime = sTime.getHours();
                    if (sHTime < 10) {
                        sHTime = '0' + sHTime;
                    }
                    var sMTime = sTime.getMinutes();
                    if (sMTime < 10) {
                        sMTime = '0' + sMTime;
                    }
                    var eHTime = eTime.getHours();
                    if (eHTime < 10) {
                        eHTime = '0' + eHTime;
                    }
                    var eMTime = eTime.getMinutes();
                    if (eMTime < 10) {
                        eMTime = '0' + eMTime;
                    }
                    var sMonthTime = sTime.getMonth() + 1;
                    if (sMonthTime < 10) {
                        sMonthTime = '0' + sMonthTime;
                    }
                    var sDateTime = sTime.getDate();
                    if (sDateTime < 10) {
                        sDateTime = '0' + sDateTime;
                    }
                    var eMonthTime = eTime.getMonth() + 1;
                    if (eMonthTime < 10) {
                        eMonthTime = '0' + eMonthTime;
                    }
                    var eDateTime = eTime.getDate();
                    if (eDateTime < 10) {
                        eDateTime = '0' + eDateTime;
                    }
                } else {
                    item.long_unit = this.payments[item.order.paymentMethod].substr(0, this.payments[item.order.paymentMethod].length - 1);
                }
                if (item.orderType == 2) {
                    item.rstartTime = sHTime + ':' + sMTime;
                    item.rendTime = eHTime + ':' + eMTime;
                } else if (item.orderType == 1) {
                    item.rstartTime = sMonthTime + '/' + sDateTime;
                    item.rendTime = eMonthTime + '/' + eDateTime;
                } else {
                    item.rstartTime = sMonthTime + '/' + sDateTime;
                    item.rendTime = eMonthTime + '/' + eDateTime;
                }
                if (title) {
                    let order_startTime = dayjs(item.order.startTime).format('YYYY-MM-DD');
                    let c_time = dayjs().format('YYYY-MM-DD');
                    let compareTime = dayjs().format('YYYY-MM-DD') + ' ' + '08:00:00';
                    if ((dayjs(c_time).unix() < dayjs(compareTime).unix() && dayjs(order_startTime).unix() < dayjs(compareTime).unix()) || (dayjs(c_time).unix() > dayjs(compareTime).unix() && dayjs(order_startTime).unix() < dayjs(c_time).unix())) {
                        item.unRoom_toDay = true;
                    } else {
                        item.unRoom_toDay = false;
                    }
                }
            }
        },
        // 跳到二维码页面
        goCode(item) {
            this.info = {};
            this.info.scaleName = item.roomScale.scaleName;
            this.info.roomSn = item.roomSn;
            this.info.orderId = item.orderId;
            this.$router.push({
                path: '/quickLiveOnline',
                query: {
                    fenfangInfo: this.info
                }
            });
            localStorage.setItem('fenfangInfo', JSON.stringify(this.info));
        },

        // 取消订单
        btncancel(item) {
            MessageBox.confirm('', {
                message: '取消订单后，房费和押金将退还顾客，请慎重操作',
                title: '取消订单',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        let orderRoomId = item.id;
                        let data = {
                            orderId: item.orderId
                        };
                        PostBusinessOrderroomCancel(orderRoomId, data).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            this.list = [];
                            this.currentPage = 1;
                            this.loadMore();
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 提前退房or退租
        btnLeaveroom(item) {
            if (item.orderType == 3 || item.orderType == 4) {
                this.$router.push({
                    path: '/rentCount',
                    query: {
                        orderRoomId: item.id,
                        rentCount_info: item
                    }
                });
                localStorage.setItem('rentCount_info', JSON.stringify(item));
            } else {
                const title = `<div>退房后，将不可恢复，请慎重操作</div>`;
                const title_ = `<div style='color:red'>此订单已续住，退房将退回续住的房费，是否继续退房？</div>`;
                MessageBox.confirm('', {
                    message: item.conRoom == '1' ? title_ : title,
                    title: '确定退房?',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                })
                    .then((action) => {
                        if (action == 'confirm') {
                            let data = {
                                orderId: item.orderId,
                                orderRoomId: item.id
                            };
                            PostBusinessOrderroomLeave(data).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                this.list = [];
                                this.currentPage = 1;
                                this.loadMore();
                            });
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        },
        // 换房
        btnamend(item) {
            this.$router.push({
                name: 'Amend',
                query: {
                    item_amend: item
                }
            });
            localStorage.setItem('item_amend', JSON.stringify(item));
        },
        // 换房(未办理入住）
        btnchange(item) {
            this.$router.push({
                path: '/fdchange',
                query: {
                    orderDetail: item
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(item));
        },
        // 办理入住
        btnlive(item) {
            this.$router.push({
                name: 'DealLive',
                params: {
                    orderDetail: item
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(item));
        },
        // cooper-换房
        btnchange_cooper(order) {
            this.cooperOrderAmend = order.id;
            this.cooperOrderId = order.orderId;
            this.cooperToScaleId = order.roomScaleId;
            GetBusinessRoomFreeCooperByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.cooperChangeRooms = res.data;
                this.cooperRooms = true;
            });
        },
        choosedRoomcancle() {
            this.cooperRooms = false;
        },
        choosedRoom(item) {
            let params = {
                orderId: this.cooperOrderId,
                toScaleId: this.cooperToScaleId,
                toVirtualRoomScaleId: '0',
                toRoomId: item.id,
                reason: this.reason
            };
            PostBusinessOrderroomRoomchange(this.cooperOrderAmend, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.cooperRooms = false;
                this.list = [];
                this.orderPageList();
            });
        }
    }
};
</script>
<style scoped>
p {
    margin: 0;
}
._flex {
    display: flex;
    align-items: center;
}
.f12 {
    font-size: 12px;
    border: 1px solid #ff7d41;
    color: #ff7d41;
    line-height: 16px;
    margin-right: 5px;
    border-radius: 2px;
    padding: 0 2px;
}
.c_2 {
    border: 1px solid #2e7bf7;
    color: #2e7bf7;
}
.c_3 {
    border: 1px solid #02bc6c;
    color: #02bc6c;
}
.c_4 {
    border: 1px solid #02bc6c;
    color: #02bc6c;
}

.per_info {
    line-height: 50px;
    border-bottom: 1px solid #f6f6f6;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.center {
    padding: 0 10px;
    background-color: #fff;
    margin-top: 10px;
}
.center:nth-child(1) {
    margin-top: 0px;
}
.per_bottom {
    padding-top: 5px;
    display: flex;
    justify-content: flex-end;
    padding: 8px 0 4px 0;
}
.btn_left {
    color: #888888;
    font-size: 12px;
    padding: 7px 5px;
    border: 1px solid #ececec;
    border-radius: 15px;
    margin-bottom: 5px;
    width: 20%;
    margin-left: 5px;
}
.cooper-change-rooms {
    width: 100%;
    height: cala(100vh - 4.5rem);
}
.lineheight {
    border-bottom: 1px solid #f6f6f6;
    line-height: 20px;
}
.borderbottom {
    border-bottom: 1px solid #dadada;
}
.roomsScroll {
    max-height: 400px;
    overflow-y: scroll;
}
.h10 {
    height: 10px;
    background: #f6f6f6;
}
.my-invoice {
    border-radius: 5px;
    font-size: 10px;
    background: #ffe6e7;
    color: #fb575b;
    line-height: 20px;
    display: inline-block;
    padding: 2px 4px;
    margin-left: 4px;
}
.noRoom_box {
    text-align: left;
    font-weight: bold;
    display: flex;
    align-items: center;
    color: #000;
    border-radius: 8px;
    background: #fff;
    margin: 8px 0;
}
.kong {
    width: 3px;
    height: 20px;
    background: #ff7d41;
}
.noLock {
    width: 18px;
    height: 18px;
    vertical-align: middle;
}
::v-deep .mint-msgbox-confirm {
    color: red;
}
</style>



