
<template>
  <div style="background-color: #F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;">
    <van-search
      v-model="servalue"
      placeholder="请输入姓名/手机号/订单号"
    />
    <div class="arrive">
      <mt-navbar
        v-model="selected"
        v-if="!$route.query.roomId"
      >
        <mt-tab-item
          @click.native="btnSearch(1)"
          id="1"
        >
          <span>未办理({{ total1 }})</span>
        </mt-tab-item>
        <mt-tab-item
          @click.native="btnSearch(2)"
          id="2"
        >
          <span>入住中({{ total2 }})</span>
        </mt-tab-item>
        <mt-tab-item
          @click.native="btnSearch(4)"
          id="4"
        >
          <span>清洁中({{ total4 }})</span>
        </mt-tab-item>
        <mt-tab-item
          @click.native="btnSearch(3)"
          id="3"
        >
          <span>已取消({{ total3 }})</span>
        </mt-tab-item>
        <mt-tab-item
          @click.native="btnSearch(7)"
          id="7"
        >
          <span>已完成</span>
        </mt-tab-item>
      </mt-navbar>
    </div>
    <div>
      <Arrive
        ref="arrive"
        @back="come"
      ></Arrive>
    </div>
  </div>
</template>
<script>
import { Navbar, TabItem, Search } from 'mint-ui';
import Arrive from './Arrive.vue';
export default {
    components: {
        Arrive,
        Navbar,
        TabItem
    },
    data() {
        return {
            selected: '1',
            item: 1,
            servalue: '',
            Arrive: '',
            value: '',
            total1: '',
            total2: '',
            total3: '',
            total4: ''
            // total6: ''
            // statusPage: 1
        };
    },
    created() {},
    activated() {
        // this.servalue = '';
        if (!this.$route.query.roomId) {
            // window.sessionStorage.setItem('formType', 'homeDel');
            this.hotelId = this.$route.query.hotelId;
            this.servalue = '';
            this.selected = this.statusPage = '1';
            this.total1 = this.total2 = this.total3 = this.total4 = '';
            this.$store.commit('setHeader', '订单管理');
        } else {
            this.$store.commit('setHeader', `房间${this.$route.query.roomSn}订单`);
        }
        let formType = window.sessionStorage.getItem('formType');
        if (this.hotelId !== this.$route.query.hotelId || formType == 'home') {
            window.sessionStorage.setItem('formType', 'homeDel');
            this.hotelId = this.$route.query.hotelId;
            this.servalue = '';
            this.selected = this.statusPage = '1';
            this.total1 = this.total2 = this.total3 = this.total4 = '';
        }
        if (this.$store.state.orderTop&&this.$store.state.orderTop.m_statu) {
            this.selected = this.statusPage = this.$store.state.orderTop.m_statu + '';
        }
        if (this.$store.state.orderTop&&this.$store.state.orderTop.m_queryValue) {
            this.servalue = this.$store.state.orderTop.m_queryValue;
        }
        setTimeout(() => {
            this.btnSearch(this.statusPage);
        }, 10);
    },

    methods: {
        btnSearch(statusPage) {
            console.log("点击了搜索按钮，状态页码为 statusPage：", statusPage);
            console.log("搜索值为：servalue", this.servalue);
            this.statusPage = statusPage;
            this.$refs.arrive.reloadPage({
                queryValue: this.servalue,
                statusPage: statusPage
            });
        },
        come(total, num) {
            if (num == undefined || num == 1) {
                this.total1 = total;
            } else if (num == 2) {
                this.total2 = total;
            } else if (num == 3) {
                this.total3 = total;
            }
            else if (num == 4) {
                this.total4 = total;
            }
        }
    },
    watch: {
        servalue(neww) {
            this.btnSearch(this.statusPage);
        }
    }
};
</script>
<style scoped>
input {
    -webkit-appearance: none !important;
    border-radius: 20px 0 0 20px !important;
    text-indent: 2em;
    margin: 10px 0;
    width: 82%;
    height: 32px;
    border: 0;
    outline: none;
    box-sizing: border-box;
}
img {
    color: #ccc;
    padding: 0;
    margin: 0;
    position: relative;
    top: 20px;
    left: 45px;
}
.arrive {
    margin: 0 0 10px;
    line-height: 45px;
    color: #ccc;
    justify-content: space-around;
}
p {
    margin: 0;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #000;
    margin-bottom: 0;
    border-bottom: 3px solid #000;
}
::v-deep .mint-navbar .mint-tab-item {
    padding: 12px 0;
}
</style>



